import React from "react"
import { Trans } from "gatsby-plugin-react-i18next"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {
  faFacebookF,
  faTwitter,
  faLinkedinIn,
  faRedditAlien,
  faWhatsapp,
} from "@fortawesome/free-brands-svg-icons"
import {
  FacebookShareButton,
  TwitterShareButton,
  LinkedinShareButton,
  RedditShareButton,
  WhatsappShareButton,
  EmailShareButton,
} from "react-share"
import Email from "../assets/svg/email.svg"

const Share = ({ url, title, twitterHandle }) => (
  <div className="post-social">
    <h3><Trans i18nKey="blog:share.inspire">Inspire a friend</Trans></h3>
    <div className="post-social-link">
      <FacebookShareButton
        url={url}
        className="button is-outlined is-rounded facebook"
      >
        <span className="icon">
          <FontAwesomeIcon icon={faFacebookF} size="sm" />
        </span>
        <span className="text">Facebook</span>
      </FacebookShareButton>

      <TwitterShareButton
        url={url}
        className="button is-outlined is-rounded twitter"
        title={title}
        via={twitterHandle.split("@").join("")}
      >
        <span className="icon">
          <FontAwesomeIcon icon={faTwitter} size="sm" />
        </span>
        <span className="text">Twitter</span>
      </TwitterShareButton>

      <LinkedinShareButton
        url={url}
        className="button is-outlined is-rounded linkedin"
        title={title}
      >
        <span className="icon">
          <FontAwesomeIcon icon={faLinkedinIn} size="sm" />
        </span>
        <span className="text">LinkedIn</span>
      </LinkedinShareButton>

      <RedditShareButton
        url={url}
        className="button is-outlined is-rounded reddit"
        title={title}
      >
        <span className="icon">
          <FontAwesomeIcon icon={faRedditAlien} size="sm" />
        </span>
        <span className="text">Reddit</span>
      </RedditShareButton>

      <WhatsappShareButton
        url={url}
        className="button is-outlined is-rounded whatsapp"
        title={title}
      >
        <span className="icon">
          <FontAwesomeIcon icon={faWhatsapp} size="sm" />
        </span>
        <span className="text">WhatsApp</span>
      </WhatsappShareButton>

      <EmailShareButton
        url={url}
        className="button is-outlined is-rounded email"
        title={title}
      >
        <span className="icon">
          <Email />
        </span>
        <span className="text">Email</span>
      </EmailShareButton>
    </div>
  </div>
)

export default Share
