import React from "react"
import { graphql } from "gatsby"
import { Link, Trans } from "gatsby-plugin-react-i18next"
import Layout from "../../components/Layout"
import PostLinks from "../../components/PostLinks"
import Share from "../../components/Share"
import { MDXRenderer } from "gatsby-plugin-mdx"
import BlogCard from "../../components/BlogCard"

const BlogTemplate = ({ data, pageContext }) => {
  const { mdx, site, allMdx } = data
  const { frontmatter, body } = mdx
  const { edges } = allMdx
  const { siteMetadata } = site
  const { prev, next } = pageContext

  return (
    <Layout>
      <main className="post">
        <header className="post-header">
          <div className="title-content">
            <h1>
              <Trans i18nKey={`posts:${frontmatter.date}.title`}>
                {frontmatter.title}
              </Trans>
            </h1>
            <blockquote className="phrase">
              <cite>
                <q>
                  <Trans i18nKey={`posts:${frontmatter.date}.phrase`}>
                    {frontmatter.phrase}
                  </Trans>
                </q>
              </cite>
              <p className="phrase-author">~ {frontmatter.author} ~</p>
            </blockquote>
            <strong>
              <Trans i18nKey={`posts:${frontmatter.date}.date`}>
                {frontmatter.date}
              </Trans>
            </strong>
          </div>
        </header>
        <section className="post-content">
          <MDXRenderer>{body}</MDXRenderer>
        </section>
        <section>
          <Share
            url={siteMetadata.url}
            title={frontmatter.path}
            twitterHandle={siteMetadata.twitterUsername}
          />
          <PostMoreInfo
            currentPost={frontmatter}
            posts={edges}
            prev={prev}
            next={next}
          />
          {/*<Comment />*/}
        </section>
      </main>
    </Layout>
  )
}

const PostMoreInfo = ({ currentPost, posts, prev, next }) => {
  let count = 0

  const relatedPosts = posts
    .filter(post => {
      if (
        currentPost.title !== post.node.frontmatter.title &&
        count < 2 &&
        post.node.frontmatter.categories.map(cat =>
          currentPost.categories.find(currentCat => cat === currentCat)
        ) !== undefined &&
        post.node.frontmatter.tags.map(tag =>
          currentPost.tags.find(currentTag => tag === currentTag)
        ) !== undefined
      ) {
        count++
        return post
      } else if (count >= 2) {
        return
      }
    })
    .map(post => <BlogCard key={post.node.id} post={post.node} />)

  return (
    <div className="post-more-info">
      {relatedPosts.length > 0 ? (
        <div className="related">
          <h3>Related Posts</h3>
          {relatedPosts}
        </div>
      ) : null}
      <PostLinks prev={prev} next={next} />
      <div className="all">
        <Link className="all-button" to="/">
          <Trans i18nKey="blog:blog-template.all">View All Articles</Trans>
        </Link>
      </div>
    </div>
  )
}

const Comment = () => (
  <div className="comment">
    <div>
      <h3>Leave a comment</h3>
    </div>
    <div>
      <h3># Coment(s)</h3>
    </div>
  </div>
)

export const query = graphql`
  query($blogPath: String) {
    site {
      siteMetadata {
        url
        twitterUsername
      }
    }
    mdx(frontmatter: { path: { eq: $blogPath } }) {
      body
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        image {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        tags
        categories
        phrase
        author
      }
    }
    allMdx(sort: { order: DESC, fields: [frontmatter___date] }, limit: 3) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            path
            title
            tags
            categories
            desc
            image {
              childImageSharp {
                fluid(maxWidth: 1200) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
      totalCount
    }
  }
`

export default BlogTemplate
